<template>
<div>
  <div class="home" v-if="this.user == 0">
    <h1>{{ title }}</h1>
    <h2>Project of a SPA using public resources from github. (Demo version. The css will be redone. I'M BACKEND !!!)</h2>
    <h4>That shit must be reactive and responsive. If itâ€™s not like that on your device, let me know!</h4>  
    <p class="search-devs">Search Devs:</p>   
    <label for="username">
        <input v-on:keyup.enter="getUser" id="search" type="text" name='username'  placeholder="GitHub name" class='form-control' required >
    </label> 
  </div> 

  <div class="grid-container" id="profile" v-if="user != 0">
    <div class="conteiner-profile">
          <Profile :user="user"/>
      </div>
    <div class="conteiner-repository">
        <Repo v-for="repo in repos" :key="repo" :repo="repo"/>
    </div>
  </div> 
</div>
</template>

<script>
import Profile from './components/Profile'
import axios from 'axios'
import Repo from './components/Repo'

export default {

  data(){
    return{
      title: "Frontend challenge",
      github:{
        url:'https://api.github.com/users',
        client_id: '',
        client_secret: '',
        count: 7,
        sort: 'create_at'
      },
      user:[],
      repos:[],
    }
  }, 

  components: {
      Profile,
      Repo,
  },

  methods:{
    getUser(e){
      const user = e.target.value;

      const {url} = this.github;
      axios.get(`${url}/${user}`)
           .then(({data}) => (this.user = data));

      axios.get(`${url}/${user}/repos`)
           .then(({data}) => this.repos = data);
    }
  },
}

</script>

